import { Component } from '@angular/core';

import {IonicPage, Platform} from 'ionic-angular';
import {FunctionsProvider} from "../../providers/functions/functions";
import {AngularFireAuth} from 'angularfire2/auth';

@IonicPage()
@Component({
  selector: 'events-tab-page',
  templateUrl: 'events-tabs.html'
})
export class EventsTabsPage {
  home: string = "about";
    private bid: any = '1';

  constructor(private  func: FunctionsProvider,
              private  afs: AngularFireAuth,


  ){

      this.afs.authState.subscribe(res => {
          if (res && res.uid) {

              this.func.sync(res.uid,this.bid)

          } else {

              // this.navCtrl.push('Branch');

          }
      });

  }

  page1 = 'LocalEventsPage';
  page2 = 'RegionalEventsPage';
  page3 = 'NationalEventsPage';
}



